<template>
    <div class="listMenu">
        <router-link :to="titleLink" class="titleMenu" :class="{ on : isActive(titleLink) }" @click="setActive(titleLink); selectItem(null)">{{ titleMenu }}</router-link>
        <ul v-if="listMenu">
            <li v-for="menu in listMenu" :key="menu">
                <router-link :to="menu.link" class="listMenu" :class="{ on : isActive(menu.title) }" @click="setActive(menu.title); selectItem(menu.category)">{{ menu.title }}</router-link>
                <ul v-if="menu.submenu" class="listSubmenu">
                    <li v-for="sub in menu.submenu" :key="sub">
                        <router-link :to="sub.link">{{ sub.title }}</router-link>
                    </li>
                </ul>
            </li>                
        </ul>
    </div>
</template>

<script>
export default {
    name: "ListmenuComponent",
    props: {
        titleMenu: String,
        titleLink: String,
        listMenu: Array
    },
    data() {
        return {
            activeTitle: this.titleLink
        }
    },
    methods: {
        setActive(title) {
            this.activeTitle = title;
        },
        isActive(title) {
            return this.activeTitle === title;
        },
        selectItem(item){
            this.$emit('itemSelected', item);
        }
    }
}
</script>