<template>
    <div class="pagination">
        <span v-if="leftmostPage > PAGE_PER_SECTION" @click="()=>{leftmostPage = 1; onChangeCurPage(leftmostPage)}">
            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M11.0458 16.0093L5.57612 10.5396C5.28322 10.2467 5.28322 9.77185 5.57612 9.47895L11.0458 4.00928" stroke="#1C1C1C" stroke-linecap="round" stroke-linejoin="round"/>
                <path d="M15.0458 16.0093L9.57612 10.5396C9.28322 10.2467 9.28322 9.77185 9.57612 9.47895L15.0458 4.00928" stroke="#1C1C1C" stroke-linecap="round" stroke-linejoin="round"/>
            </svg>
        </span>
        <span v-if="leftmostPage > PAGE_PER_SECTION" @click="()=> {leftmostPage -= PAGE_PER_SECTION; onChangeCurPage(leftmostPage)}">
            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M13.046 16.0093L7.84148 10.8048C7.40208 10.3654 7.40208 9.6531 7.84148 9.2138L13.046 4.0093" stroke="#1C1C1C" stroke-linecap="round" stroke-linejoin="round"/>
            </svg>
        </span>
        <ul class="page">
            <li
                :class="{ on : page === curPage }"
                @click="onChangeCurPage(page)"
                v-for="page in getPaginationArray(leftmostPage)"
                :key="page"
            >
                {{ page }}
            </li>
        </ul>
        <span v-if="leftmostPage + PAGE_PER_SECTION <= totalPage" @click="()=>{leftmostPage += PAGE_PER_SECTION; onChangeCurPage(leftmostPage)}">
            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M6.95398 3.99072L12.1585 9.19522C12.5979 9.63457 12.5979 10.3469 12.1585 10.7862L6.95398 15.9907" stroke="#1C1C1C" stroke-linecap="round" stroke-linejoin="round"/>
            </svg>
        </span>
        <span v-if="leftmostPage + PAGE_PER_SECTION <= totalPage" @click="()=>{leftmostPage = totalPage - ((totalPage - 1) % 5); onChangeCurPage(totalPage)}">
            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M8.95423 3.99072L14.4239 9.4604C14.7168 9.75327 14.7168 10.2281 14.4239 10.521L8.95423 15.9907" stroke="#1C1C1C" stroke-linecap="round" stroke-linejoin="round"/>
                <path d="M4.95423 3.99072L10.4239 9.4604C10.7168 9.75327 10.7168 10.2281 10.4239 10.521L4.95423 15.9907" stroke="#1C1C1C" stroke-linecap="round" stroke-linejoin="round"/>
            </svg>
        </span>
    </div>
</template>

<script>
import { computed, ref } from 'vue';

export default {
    name: "PaginationComponent",
    props:{
        list: Array,
        ITEM_PER_PAGE: Number,
        PAGE_PER_SECTION : Number,
    },
    emits: ['change-page'],
    setup(props, { emit }){
        return {
            ...usePagination(props, emit),
        }
    }
}

const usePagination = (props, emit) => {
    const curPage = ref(1);
    const leftmostPage = ref(1);
    const totalPage = computed(()=> {
        return Math.ceil(props.list.length / (props.ITEM_PER_PAGE || 1));
    })

    const getPaginationArray = (left) => {
        const res = [];
        for (let i = left; i < Math.min(totalPage.value + 1, left + props.PAGE_PER_SECTION); i++){
            res.push(i);
        }
        return res;
    }

    const onChangeCurPage = (page) => {
        curPage.value = page;
        emit('change-page', page);
    }

    return {
        leftmostPage,
        curPage,
        totalPage,
        getPaginationArray,
        onChangeCurPage,
    }
}
</script>