<script>
import SubMenuComponent from './SubMenuComponent.vue';
export default {
  components: { SubMenuComponent },
  name: 'HeaderComponent',
    data(){
        return{
            allMenu:false,
            eventMenu:false,
            contactMenu:false,
            shoppingBag:false,
            allProduct:[
                { title: "딥펜/만년필", titleLink: "/dip-pen", menuList: ["잉크", "딥펜/만년필", "펜파우치"] },
                { title: "문구", titleLink: "/", menuList: ["컬러 차트/스카치", "노트/플래너", "낱장 지류", "팬시"] },
                { title: "독서", titleLink: "/", menuList: ["독서기록", "북퍼퓸", "악세서리"] },
                { title: "디지털", titleLink: "/", menuList: ["디지털 다이어리&노트", "디지털 스티커"] },
                { title: "향/디퓨저", titleLink: "/", menuList: ["디퓨저", "인세스", "오일 방향제"] },
                { title: "리빙/라이브", titleLink: "/", menuList: ["보틀", "섬유", "인테리어", "기타"] }
            ],
            event:[
                { title:"기획전", titleLink: "/"},
                { title:"무료 배경화면", titleLink: "/"},
                { title:"굿즈/콜라보", titleLink: "/", menuList: ["네이버 웹툰", "Synav:시나브"] },
            ],
            contact:[
                { title:"견적문의", titleLink: "/"},
                { title:"FAQ", titleLink: "/"}
            ]
        }
    },
    methods: {
        openSubmenu(menuType) {
            this.allMenu = menuType === 'all';
            this.eventMenu = menuType === 'event';
            this.contactMenu = menuType === 'contact';
        },
        closeSubmenu(menuType) {
            console.log('Close submenu:', menuType);
            if (menuType === 'all') {
                this.allMenu = false;
            } else if (menuType === 'event') {
                this.eventMenu = false;
            } else if (menuType === 'contact') {
                this.contactMenu = false;
            }
            console.log('Current state after close:', this.allMenu, this.eventMenu, this.contactMenu);
        },
        shoppingBagToggle() {
            this.shoppingBag = !this.shoppingBag
        }
    },
    created(){
        this.$router.beforeEach((to, from, next) => {
            this.allMenu = false;
            this.eventMenu = false;
            this.contactMenu = false;
            next();
        })
    }
}
</script>